import restaurantIdb from '../../data/local';
import { INFO } from '../../globals/variables';
import { createRestaurantCard, warningIconTemplate } from '../templates/template-creator';

const Like = {
  async render() {
    return `
    <div id="ldsContainer" class="lds-container">
      <div class="lds-ellipsis">
        <div></div><div></div><div></div><div></div>
      </div>
    </div>
      <h1 class="title" style="padding: 0;">Daftar Restoran Favorit</h1>
      <div id="infoContainer" style="display: none; flex-direction: column; justify-content: center; align-items: center; width: 100%; height: 80vh; max-width: 100vw;">
        <div class="warning-icon" style="display: grid; justify-content: center;"></div>
        <p id="infoText" style="font-size: 12pt;"></p>
      </div>
      <div id="maincontent" tabindex="-1">
        <div class="cards-container" id="cardsContainer"></div>
      </div>
    `;
  },

  renderNoConnection() {
    this.hideLoading();
    const titleElement = document.querySelector('.title');
    titleElement.style.display = 'none';

    const infoContainerElement = document.querySelector('#infoContainer');
    infoContainerElement.style.display = 'flex';

    const warningIconElement = document.querySelector('.warning-icon');
    warningIconElement.innerHTML = warningIconTemplate();

    this.showInfo(INFO.NO_CONNECTION);
  },

  showInfo(text) {
    const infoText = document.querySelector('#infoText');
    infoText.innerHTML = text;
  },

  hideInfo() {
    const infoContainer = document.querySelector('#infoContainer');
    infoContainer.style.display = 'none';
  },

  hideLoading() {
    const ldsContainer = document.querySelector('#ldsContainer');
    ldsContainer.style.display = 'none';
  },

  async afterRender() {
    const restaurants = await restaurantIdb.getAll();

    if (restaurants.length === 0) {
      this.showInfo('Belum menyimpan restoran favorit.');
    } else {
      this.hideInfo();
    }

    this.hideLoading();

    const restaurantsContainer = document.querySelector('#cardsContainer');

    restaurants.forEach((restaurant) => {
      restaurantsContainer.innerHTML += createRestaurantCard(restaurant);
    });
  },
};

export default Like;
