const createRestaurantCardSkeleton = () => `<article class="restaurant-card" style="width: 100%;">
  <div class="skeleton" style="width: 100%; height: 14rem;"></div>
  <section style="grid-gap: 0.5em;">
    <div class="flex items-center space-between">
      <div class="skeleton skeleton-title"></div>
      <div class="skeleton skeleton-rating"></div>
    </div>
    <div class="skeleton skeleton-city"></div>
    <div class="skeleton skeleton-description"></div>
    <div class="skeleton skeleton-description"></div>
    <div class="skeleton skeleton-description"></div>
    <div class="skeleton skeleton-description"></div>
  </section>
</article>`;

const createRestaurantCard = (restaurant) => `<article class="restaurant-card">
    <div class="img-container">
      <a href="#/detail/${restaurant.id}" style="width: 100%;">
        <picture>
          <source media="(max-width: 600px)" srcset="${API_ENDPOINT.IMG_SMALL}/${restaurant.pictureId}">
          <img id="restaurantImg-${restaurant.id}" onload="this.classList.remove('skeleton');"
            loading="lazy" class="skeleton lazyload" crossorigin="anonymous" data-src="${API_ENDPOINT.IMG_MEDIUM}/${restaurant.pictureId}" alt="restoran ${restaurant.name}" />
        </picture>
      </a>
    </div>
    <a href="#/detail/${restaurant.id}">
      <section>
        <div class="flex items-center space-between">
          <p class="bold">${restaurant.name}</p>
          <p class="flex items-center"><span class="text-lg text-darkyellow">★</span> ${restaurant.rating}</p>
        </div>
        <p>Kota: <span class="text-bold">${restaurant.city}</span></p>
        <p>${restaurant.description}</p>
      </section>
    </a>
  </article>`;

const createRestaurantDetailTemplate = (restaurant) => {
  const template = `
  <article class="body">
    <div class="header">
      <h1 id="name">${restaurant.name}</h1>
      <p>Rating: <span>${restaurant.rating} ★</span></p>
    </div>
    <p class="m-0">Kota: <span class="text-bold">${restaurant.city}</span></p>
    <p id="address" class="address">${restaurant.address}</p>
    <section class="menus-container">
      <div class="menu-container">
        <h2>Menu Makanan</h2>
        <ul id="foodList" class="menu-list"></ul>
      </div>
      <div class="menu-container">
        <h2>Menu Minuman</h2>
        <ul id="drinkList" class="menu-list"></ul>
      </div>
      <p id="description" style="font-size: 12pt"></p>
    </section>
    <p style="font-size: 12pt">${restaurant.description}</p>
    <h2>Costumer Reviews</h2>
    <section id="reviewsContainer" class="reviews-container"></section>
  </article>
  `;

  return template;
};

