import RestaurantRemote from '../../data/remote';
import restaurantIdb from '../../data/local';
import { createRestaurantCard, createRestaurantCardSkeleton, warningIconTemplate } from '../templates/template-creator';
import { INFO } from '../../globals/variables';

const list = {
  async render() {
    return `
      <div class="jumbotron relative">
        <div class="hero__image-container">
          <picture class="hero__image">
            <source media="(max-width: 768px)" srcset="images/heros/hero-image_2_40-small.jpg" alt="gambar hero atau background">
            <img class="w-full" src="images/heros/hero-image_2_40-large.jpg" alt="gambar hero atau background" />
          </picture>
        </div>
        <div class="hero__inner" style="margin-top: 8em; max-width: 80%;">
          <h1 class="hero__title">Memberikan informasi berbagai restoran di Indonesia!</h1>
          <p class="hero__tagline">Ingin mencari restoran terdekat? restoran yang terjamin baik? atau ingin mencari
            restoran paling populer saat ini? <span>Disinilah tempatnya!</span></p>
        </div>
      </div>
      <h2 class="title">Daftar Restoran</h2>
      <p class="description">paling populer di Indonesia!</p>
      <div id="infoContainer" style="display: none; flex-direction: column; justify-content: center; align-items: center; width: 100%; height: 80vh; max-width: 100vw;">
        <div class="warning-icon" style="display: grid; justify-content: center;"></div>
        <p id="infoText" style="font-size: 12pt;"></p>
      </div>
      <div id="maincontent" tabindex="-1">
        <div class="cards-container" id="cardsContainer"></div>
      </div>
    `;
  },

  renderNoConnection() {
    const warning = document.querySelector('#infoWarning');
    warning.style.display = 'block';

    const info = document.querySelector('#infoText');
    info.style.display = 'block';
  },

  showInfo(text) {
    const infoContainer = document.querySelector('#infoContainer');
    infoContainer.style.display = 'flex';

    const infoText = document.querySelector('#infoText');
    infoText.innerHTML = text;
  },

  renderWarning() {
    const warningIconElement = document.querySelector('.warning-icon');
    warningIconElement.innerHTML = warningIconTemplate();
  },

  hideJumbotron() {
    const warningIconElement = document.querySelector('.jumbotron');
    warningIconElement.style.display = 'none';
  },

  hideTitleDescription() {
    const titleElement = document.querySelector('.title');
    titleElement.style.display = 'none';

    const descriptionElement = document.querySelector('.description');
    descriptionElement.style.display = 'none';
  },

  async handleFavoriteClick(id) {
    await restaurantIdb.delete(id);
    this.renderFavorite(id);
  },

  showSkeleton() {
    const cardsContainerElement = document.querySelector('#cardsContainer');
    const totalCards = 13;

    // eslint-disable-next-line no-plusplus
    for (let i = 0; i < totalCards; i++) {
      cardsContainerElement.innerHTML += createRestaurantCardSkeleton();
    }
  },

  hideSkeleton() {
    const cardsContainerElement = document.querySelector('#cardsContainer');
    cardsContainerElement.innerHTML = '';
  },

  async afterRender() {
    try {
      this.showSkeleton();

      const data = await RestaurantRemote.restaurants();

      const restaurantsContainer = document.querySelector('#cardsContainer');

      this.hideSkeleton();
      data.restaurants.forEach((restaurant) => {
        restaurantsContainer.innerHTML += createRestaurantCard(restaurant);

        const img = document.querySelector(`#restaurantImg-${restaurant.id}`);
        img.onload = () => {
          img.classList.remove('skeleton');
        };
      });
    } catch (error) {
      this.hideSkeleton();
      this.hideJumbotron();
      this.hideTitleDescription();

      this.renderWarning();
      this.showInfo(INFO.NO_CONNECTION);
    }
  },
};

export default list;
